body{
    font-family: 'Roboto Slab', serif;
    padding: 0;
    margin: 0;
    overflow-x:hidden ;
   
}*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}nav{
  width: 100%;
  height: 8vh;
  background-color: rgba(36, 34, 34, 0.993);
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: -1px 1px 7px 2px rgba(0,0,0,0.5);
}.brand_name{
    color: white;
    font-size: 22px;
   
}.menu_list ul{
   display: flex;
   justify-content: space-around;
   align-items: center;
   list-style: none;
}.menu_list{
    width: 80%;
}.menu_list ul li *{
    display: inline-block;
    color: white;
    text-decoration: none;
    display: block;
    /* border-bottom: 1px solid #333333; */
}.burger{
    display: none;
}.menu_list ul li .activeHover{
    text-decoration: underline;
   
    color: rgb(224, 8, 8);
    /* background-color: rgb(151, 14, 4); */
    transition: all .24s ease-in-out;

}.card-img-top{
    width: 30vw;
    height: 110vh;
    background-position: center;
    background-size: cover;
    padding: 0;
    margin: 0px;
    /* margin-top: -30vh; */
}.card{
    background:none;
    padding: 0;
    margin: 0;
    font-family: 'Anton', sans-serif;
}
    .bg-intro{
    font-family: 'Anton', sans-serif;
}

@media (max-width:768px){
    .card-img-top{
        width: 80%;
        height: 100vh;
        margin-top: -12px;
    }
    .menu_list{
      
        top: 8vh;
        position: absolute;
        height: 92vh;
        z-index: 2;
        width: 50%;
        right: 0;
        background-color:rgba(36, 34, 34, 0.993);
        transform: translateX(100%);
        transition: all .23s ease-out;
    }.menu_list ul{
        justify-content: space-around;
        flex-direction: column;
        align-content: center;
        height: 100%;
    }
    .burger{
        display: block;
        cursor: pointer;
    }.burger div{
        width: 35px;
        height: 3px;
        margin: 5px;
        background-color: white;
        border-radius: 1.3rem;
        cursor: pointer;
    }.menu_list.active{
        transform: translateX(0);
    }
}